import React, { useState } from 'react';
import { Button, ButtonGroup } from 'react-bootstrap';
import { useSelector } from 'react-redux';
import { OrderStatusState } from '../../../../types/OrderStatus';

const OrderItemStatusButtons = (): React.ReactElement => {
  const [editMode, setEditMode] = useState<boolean>(false);

  const { orderStatus, loading, error } = useSelector<{
    orderStatus: OrderStatusState;
  }, OrderStatusState>((state) => state.orderStatus);

  return (
    <ButtonGroup>
      {orderStatus.name === 'Новые' && (
        <>
          <Button size="sm" variant="outline-primary">Подтвердить</Button>
          <Button size="sm" variant="outline-danger">Отменить</Button>
        </>
      )}
    </ButtonGroup>
  );
};

export default OrderItemStatusButtons;
